import CloseButton from "./CloseButton";
import {useState} from "react";

const AlertPopUp = () =>
{
    const [visible, setVisible] = useState(true);
    const handleCloseClick = () =>{
        setVisible(false);
    }

    return visible ? (
        <div className='App' style={{
            zIndex: '10',
            fontSize: 18,
            paddingTop: 8,
            paddingBottom: 24,
            backgroundColor: "rgba(0, 0, 0, 85%)",
            fontFamily: "NotoSansRegular",
        }}>
            <CloseButton onClick={handleCloseClick}/> <br/><br/><br/>
            <a style={{fontSize: 34, fontFamily: "fortnite", color: "rgba(55,131,255,1)"}}>Rolling into some trouble? <br/><br/></a>
            Wait a sec, then refresh! <br/>
            The server is free, so hiccups can happen. <br/><br/>
            Thanks! <br/>
        </div>
    ) : null;
}
export default AlertPopUp